.user-layout {
  width: 500px;
  height: 300px;
  margin: 0 auto;
  border: 1px solid #efefef;
  padding: 10px;
  -webkit-box-shadow: 0 0 10px 2px #eee;
          box-shadow: 0 0 10px 2px #eee;
}
.user-layout nav {
  margin-bottom: 10px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}
.user-layout nav a {
  margin: 6px;
}
.user-layout nav a.active {
  color: #e91e63;
}



